<template>
  <div class="nav-div">
    <div>
      <img src="../../assets/1.jpg" />
      <i class="iconfont iconshezhi" @click="show"></i>
    </div>
    <div>
      <el-dialog title="设置" :visible.sync="dialogConFormVisible">
        <el-form v-model="form">
          <el-form-item label="IP地址">
            <el-input v-model="form.setIp"></el-input>
          </el-form-item>
          <el-form-item label="是否开启相机检测">
            <el-switch
              v-model="form.setcamera"
              active-color="#13ce66"
              inactive-color="#ff4949"
              style="float: left; margin: 10px"
            >
            </el-switch>
          </el-form-item>
          <el-form-item label="开启系统控制">
            <el-switch
              v-model="form.setpower"
              active-color="#13ce66"
              inactive-color="#ff4949"
              style="float: left; margin: 10px"
            >
            </el-switch>
          </el-form-item>
          <!-- <el-form-item label="是否检测托盘">
            <el-switch
              v-model="form.settray"
              active-color="#13ce66"
              inactive-color="#ff4949"
              style="float: left; margin: 10px"
            >
            </el-switch>
          </el-form-item> -->
          <el-form-item label="光电触发开关">
            <el-switch
              v-model="form.setPhotoelectricity"
              active-color="#13ce66"
              inactive-color="#ff4949"
              style="float: left; margin: 10px"
            >
            </el-switch>
          </el-form-item>
        </el-form>
        <div slot="footer">
          <el-button type="danger" @click="reSet">复位</el-button>
          <el-button @click="clear">取消</el-button>
          <el-button type="primary" @click="set_up">确认</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  name: "NavMenu",
  inject: ["reload"],
  data() {
    return {
      dialogConFormVisible: false,
      tasks: [],
      form: {
        setcamera: false,
        setpower: false,
        //settray: false,
        setPhotoelectricity: false,
        setIp: "192.168.1.224",
      },
      
    };
  },
  methods: {
    clear() {
      this.dialogConFormVisible = false;
    },
    show() {
      this.loadSet();
      this.dialogConFormVisible = true;
    },
    loadSet() {
      this.$axios
        .get(
          "http://localhost:52000/config/find?id=601764207ab6bd57abbe0af0",
          {}
        )
        .then((resp) => {
          if (resp.data.data != null) {
            this.form.setcamera = resp.data.data.setcamera;
            this.form.setpower = resp.data.data.setpower;
            //this.form.settray = resp.data.data.settray;
            this.form.setPhotoelectricity = resp.data.data.setPhotoelectricity;
            this.form.setIp = resp.data.data.setIp;
          }
        });
    },
    set_up() {
      this.$confirm("确认更改设置？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.$axios
          .post("http://localhost:52000/config/save", {
            id: "601764207ab6bd57abbe0af0",
            setcamera: this.form.setcamera,
            setpower: this.form.setpower,
            //settray: this.form.settray,
            setPhotoelectricity: this.form.setPhotoelectricity,
            setIp: this.form.setIp,
          })
          .then((resp) => {
            this.$emit("set_up");
            this.dialogConFormVisible = false;
          });
      });
    },
    reSet() {
      this.$confirm("确认复位？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        //关闭运单序列的运单入口
        this.$axios
          .get("http://localhost:52000/api/task/get", {})
          .then((resp) => {
            this.tasks = resp.data.data;
            for (let i = 0; i < this.tasks.length; i++) {
              this.$axios.post(
                "http://localhost:7100/api/route/orderSequences/" +
                  this.tasks[i].taskId +
                  "/markComplete",
                {}
              );
            }
          });

        //删除数据库
        this.$axios.delete(
          "http://localhost:52000/web/waybilltask/deleteAll",
          {}
        );
        this.$axios.delete("http://localhost:52000/api/task/deleteALL", {});
        //重置设置
        this.$axios
          .post("http://localhost:52000/config/save", {
            id: "601764207ab6bd57abbe0af0",
            setcamera: false,
            setpower: false,
            setPhotoelectricity: false,
            setIp: "192.168.1.224",
          })
          .then((resp) => {
            this.$emit("set_up");
            this.dialogConFormVisible = false;
            this.reload();
          });
      });
    },
  },
};
</script>

<style>
.nav-div {
  width: 100%;
  background-color: rgb(253, 209, 0);
  overflow: hidden;
}

.nav-div img {
  width: 110px;
  float: left;
  margin: auto 20px;
}

.el-icon-switch-button {
  cursor: pointer;
  outline: 0;
  float: right;
  font-size: 40px;
  color: #222;
  padding: 10px;
}

.iconshezhi {
  float: right;
  margin-top: 13px;
  margin-right: 13px;
  font-size: 28px;
}
</style>


